import React, { useEffect, useState } from "react";

const checkRole = () => {
  if (typeof window === "undefined") {
    // Return default role jika dijalankan di server
    return "user";
  }

  const role = localStorage.getItem("token"); // Ambil token dari localStorage
  return role || "user"; // Return default "user" jika tidak ada token
};

interface LayoutProps {
  admin: React.ReactNode;
  teacher: React.ReactNode;
  user: React.ReactNode;
}

const Layout: React.FC<LayoutProps> = ({ admin, teacher, user }) => {
  const [role, setRole] = useState("");

  useEffect(() => {
    setRole(checkRole());
  }, []);
  return (
    <div>
      <div>{role === "admin" ? admin : role === "user" ? user : teacher}</div>
    </div>
  );
};

export default Layout;
